.yachts-banner {
    background: 
    -webkit-gradient(
      linear, 
      left top, left bottom, 
      from(rgba(1, 13, 22, 0.60)), 
      to(rgba(1, 13, 22, 0.60))
    ),
    url("https://www.lawsonrobb.com/wp-content/uploads/2015/02/superyacht_interior_design_stern-2400x1368.jpg") no-repeat center center fixed;
    background: 
    -webkit-linear-gradient(
      rgba(1, 13, 22, 0.60), 
      rgba(1, 13, 22, 0.60)
    ),
    url("https://www.lawsonrobb.com/wp-content/uploads/2015/02/superyacht_interior_design_stern-2400x1368.jpg") no-repeat center center fixed;
    background: 
    -o-linear-gradient(
      rgba(1, 13, 22, 0.60), 
      rgba(1, 13, 22, 0.60)
    ),
    url("https://www.lawsonrobb.com/wp-content/uploads/2015/02/superyacht_interior_design_stern-2400x1368.jpg") no-repeat center center fixed;
    background: 
    linear-gradient(
      rgba(1, 13, 22, 0.60), 
      rgba(1, 13, 22, 0.60)
    ),
    url("https://www.lawsonrobb.com/wp-content/uploads/2015/02/superyacht_interior_design_stern-2400x1368.jpg") no-repeat center center fixed;
    background-size: cover;
    height: 30em;
    width: 100%;
}

.yachts-description-container {
    padding: 7% 0% 0% 30%;
}

.yachts-banner-description {
    font-family: 'Cinzel', serif;
    line-height: 75px;
    text-align: center;
    font-size: 48px;
    color: white;
    width: 500px;
}

.yachts-page-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-left: 7.5%;
    margin-right: 7.5%;
}

.yachts-description {
    color: black;
    font-family: 'Source Serif Pro', serif;
    font-weight: 400;
    text-align: center;
    margin: 0;
}

.yachts-picture-group {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.display-image {
    height: 30em;
    width: 40%;
    margin: 5%;
}

@media (max-width: 768px) { 
    .yachts-banner {
        background: 
        -webkit-gradient(
          linear, 
          left top, left bottom, 
          from(rgba(1, 13, 22, 0.60)), 
          to(rgba(1, 13, 22, 0.60))
        ),
        url("https://www.lawsonrobb.com/wp-content/uploads/2015/02/superyacht_interior_design_stern-2400x1368.jpg") 80% 40%;
        background: 
        -webkit-linear-gradient(
          rgba(1, 13, 22, 0.60), 
          rgba(1, 13, 22, 0.60)
        ),
        url("https://www.lawsonrobb.com/wp-content/uploads/2015/02/superyacht_interior_design_stern-2400x1368.jpg") 80% 40%;
        background: 
        -o-linear-gradient(
          rgba(1, 13, 22, 0.60), 
          rgba(1, 13, 22, 0.60)
        ),
        url("https://www.lawsonrobb.com/wp-content/uploads/2015/02/superyacht_interior_design_stern-2400x1368.jpg") 80% 40%;
        background: 
        linear-gradient(
          rgba(1, 13, 22, 0.60), 
          rgba(1, 13, 22, 0.60)
        ),
        url("https://www.lawsonrobb.com/wp-content/uploads/2015/02/superyacht_interior_design_stern-2400x1368.jpg") 80% 40%;
        height: 20em;
    }

    .yachts-description-container {
        padding: 15% 0% 0% 15%;
    }

    .yachts-banner-description {
        font-size: 32px;
    }

    .yachts-picture-group {
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-line-pack: center;
            align-content: center;
    }

    .display-image {
        width: 90% !important;
    }
}

@media (max-width: 480px) {

    .yachts-description-container {
        padding: 15% 0% 0% -10%;
    }

    .yachts-banner-description {
        text-align: left;
    }

    .yachts-picture-group {
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-line-pack: center;
            align-content: center;
    }

    .display-image {
        height: 15em !important;
    }
}


